
import { Component, Prop, Vue } from 'vue-property-decorator';
import { mixins } from 'vue-class-component';
import filmGenresMixin from '@/mixins/filmGenresMixin';
import { globalImgAddress } from '@/main.ts';
import { IGenre } from '@/interfaces/IGenre';
import { ISearchFilm } from '@/interfaces/ISearchFilm';

@Component
export default class ActorCastCrewTemplate extends mixins(filmGenresMixin) {
  globalImgAddress = globalImgAddress;

  get genresNames() {
    return this.getCurrentMediaTypeGenresNames(this.genres, this.arr.genre_ids);
  }

  @Prop()
  arr!: ISearchFilm;

  @Prop()
  index!: number;

  @Prop()
  genres!: Array<IGenre>;
}
